.header[data-v-0e1d61e8] {
  position: fixed;
  top: 20px;
  left: 10%;
  width: 80%;
  height: 60px;
  background-color: rgba(247, 247, 247, 0.9);
  border-radius: 5px;
  transition: all .3s ease;
}
.header--full[data-v-0e1d61e8] {
  top: 0;
  left: 0;
  width: 100%;
  border-radius: 0;
  background-color: rgba(247, 247, 247, 0.8);
  box-shadow: 0 5px 10px -5px rgba(105, 105, 105, 0.6);
}



